<template>
  <div class="desmoswrapper">
      <div
          class="desmos_embed w-embed w-script"
          :id="`calculator${calcId}`"
      >
        <div class="desmos-id">{{ calcId }}</div>
      </div>

      <div class="canvas-inst large-blur" :id="`canvasInst${calcId}`">
        <div>Select the expression in which you want to add tangent for.</div>
      </div>
  </div>
</template>

<script>
export default {
    name: "DesmosComp",
    props: {
        calcId: {
            type: Number,
            defaultValue: 1
        }
    },
    data: () => ({

    }),
    mounted () {
      const Desmos = window.Desmos;
      const myColors = {
        green: '#ff0057',
        blue: '#ff1e00',
        red: '#00d58e',
        pink: '#00ff00',
        yellow: '#0011ff',
        purple: '#aaff00',
        guideline: 'rgba(0, 0, 0, 0.25)',
        white: '#000000'
      }

      const elt = document.getElementById(`calculator${this.calcId}`)

      // const production = { border:false, invertedColors: true, showGrid: false, projectorMode: true, keypad:false, expressionsCollapsed: true, settingsMenu:false,lockViewport: true, fontSize:16,expressions:false};
      const development = { border:false, invertedColors: true, showGrid: false, projectorMode: true, keypad:true, expressionsCollapsed: false, settingsMenu:true,lockViewport: false, fontSize:16,expressions:true};
      window[`calculator${this.calcId}`] = Desmos.GraphingCalculator(elt, {
        colors: myColors,
        ...development
      })
    },
    methods: {

    },
}
</script>

<style scoped>
.desmos-id {
  position: absolute;
  z-index: 1000;
  bottom: 1%;
  right: 1%;
  background: #456BD9;
  border: 0.1875em solid #0F1C3F;
  border-radius: 50%;
  box-shadow: 0.375em 0.375em 0 0 rgba(15, 28, 63, 0.125);
